  font-size: 62.5%;
}

body {
  font-family: var(--font-family);
  color: var(--text);
  font-size: 1.6rem;
  width: 100%;
}

/* Landscape phones and down */
/* Landscape phone to portrait tablet */
/* Portrait tablet to landscape and desktop */
/* Large desktop */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --font-family-imperial-script: "Imperial Script";
  --font-family: "mulish";
}

.allura-regular {
  font-family: "Allura", cursive;
  font-weight: 400;
  font-style: normal;
}

.separator-1 {
  height: 2px;
  width: 20rem;
  background: white;
  margin: 2.5rem;
}

.separator-2 {
  height: 2px;
  width: 80%;
  background: white;
}

/* Landscape phones and down */
/* Landscape phone to portrait tablet */
/* Portrait tablet to landscape and desktop */
/* Large desktop */
header {
  grid-area: header;
  position: relative;
  height: 20rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(-45deg, #6420aa, #7f27ff);
  animation: header-anim 2s;
}
@media (max-width: 480px) {
  header {
    height: 10rem;
    animation: header-anim 1s;
  }
}
@keyframes header-anim {
  0% {
    opacity: 0;
    transform: translateY(-20rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}

.header-container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

h1 {
  margin: 1rem;
  font-size: 4rem;
  font-family: var(--font-family-imperial-script);
  color: #eee;
  font-weight: 200;
}

.span-h1 {
  font-family: "Roboto Mono", monospace;
  font-weight: 400;
  font-size: 2rem;
}

.btn-xs {
  display: none;
}

@media (max-width: 480px) {
  .btn-xs {
    position: absolute;
    right: 0;
    height: 2.5rem;
    width: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    transition: all 2s;
    cursor: pointer;
    margin: 1rem;
  }
}
.bar {
  height: 2px;
  width: 2.5rem;
  background: white;
  position: absolute;
  transition: all 0.3s;
}

.bar-1 {
  top: 0;
}

.bar-1-active-1 {
  top: calc(50% - 1px);
}

.bar-1-active-2 {
  rotate: 45deg;
}

.bar-2-active {
  opacity: 0;
}

.bar-3 {
  bottom: 0;
}

.bar-3-active-1 {
  bottom: calc(50% - 1px);
}

.bar-3-active-2 {
  rotate: -45deg;
}

/* Landscape phones and down */
/* Landscape phone to portrait tablet */
/* Portrait tablet to landscape and desktop */
/* Large desktop */
.info-bar {
  grid-area: nav-bar;
  min-height: 100vh;
  height: auto;
  width: 25rem;
  background: #eee;
  transition: all 1s;
  animation: info-anim 2s;
}
@media (max-width: 480px) {
  .info-bar {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 1s;
    animation: info-anim 1s;
  }
  @keyframes info-anim {
    0% {
      transform: translateX(-25rem);
    }
    100% {
      transform: translateX(0rem);
    }
  }
}
@keyframes info-anim {
  0% {
    opacity: 0;
    transform: translateX(-25rem);
  }
  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}

.info-bar-active {
  transform: translateX(-100%);
}

img {
  height: 15rem;
  width: 15rem;
  border-radius: 50%;
}

.img-container {
  height: 20rem;
  width: 25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5rem;
}
@media (max-width: 480px) {
  .img-container {
    margin: 2rem;
  }
}

.info-content {
  height: auto;
  width: 25rem;
  display: flex;
  flex-direction: column;
  align-items: start;
}
@media (max-width: 480px) {
  .info-content {
    text-align: center;
  }
}

.info-perso-container {
  height: auto;
  margin-bottom: 2rem;
}

.div-info {
  margin-left: 2rem;
}
@media (max-width: 480px) {
  .div-info {
    margin: 0;
  }
}

.p-info {
  font-style: italic;
  font-weight: 400;
  margin: 0.5rem;
}

.telephone {
  cursor: pointer;
}

.fa-phone {
  rotate: 135deg;
  transition: all 1s;
  color: #6420aa;
}

.fa-phone-active {
  rotate: 0deg;
  color: #333;
}

.telephone-num {
  opacity: 0;
  margin-left: 1rem;
  font-size: 1.6rem;
  text-decoration: none;
  transition: all 1s;
}
