  .telephone-num {
    opacity: 1;
    color: #6420aa;
  }
}

.telephone-num-active {
  opacity: 1;
  color: #6420aa;
}

.bottom {
  margin-bottom: 2rem;
}

.title {
  font-size: 2rem;
  font-weight: 400;
  color: #6420aa;
  font-weight: 700;
}

.title-phone {
  font-size: 2rem;
  font-weight: 400;
}

.mail {
  display: flex;
  font-size: 2rem;
  margin-left: 2rem;
}
@media (max-width: 480px) {
  .mail {
    justify-content: center;
    margin: 0;
  }
}

.fa-envelope {
  cursor: pointer;
  color: #6420aa;
}

.fa-envelope-active {
  display: none;
}

.fa-envelope-open {
  display: none;
}

.fa-envelope-open-active {
  display: flex;
  cursor: pointer;
}

.title-mail {
  display: flex;
  justify-content: start;
  align-items: center;
}
@media (max-width: 480px) {
  .title-mail {
    justify-content: center;
  }
}

.mail-anim {
  opacity: 0;
  font-size: 1.6rem;
  margin-left: 1.3rem;
  transition: all 1s;
}
@media (max-width: 480px) {
  .mail-anim {
    opacity: 1;
    color: #6420aa;
    margin: 0.5rem;
  }
}

.mail-anim-active {
  opacity: 1;
  color: #6420aa;
}

.langue-container {
  margin-top: 2rem;
  margin-left: 2rem;
}
@media (max-width: 480px) {
  .langue-container {
    margin: 0;
    margin-top: 2rem;
  }
}

.text {
  color: #333;
  font-size: 1.8rem;
}

.star {
  font-size: 1.4rem;
  color: #6420aa;
  margin: 1rem 0.2rem;
}

.star-1 {
  font-size: 1.4rem;
  color: #bbb;
  margin: 1rem 0.2rem;
